.home {
  background: url("../images/content/home/main-bg.jpg") center no-repeat;
  background-size: cover;
  height: 60rem;
  color: white;

  .container {
    height: 100%;
  }

  &__inner {
    height: 100%;
    max-width: 73rem;

    display: flex;
    align-items: stretch;
    justify-content: center;
    flex-direction: column;
  }

  &__title {
    font-size: 4.8rem;
    font-weight: 900;

    margin-top: 0;
    margin-bottom: 3rem;

    @media screen and (max-width: $mobile-screen) {
      font-size: 3.4rem;
    }
  }

  &__text {
    font-size: 1.8rem;
    font-weight: 600;

    margin-top: 0;
    margin-bottom: 3rem;
  }

  &__actions {
    display: flex;
    align-items: stretch;
    justify-content: flex-start;

    @media screen and (max-width: $mobile-screen) {
      flex-direction: column;
    }

    & > button:not(:last-child) {
      margin-right: 3rem;

      @media screen and (max-width: $mobile-screen) {
        margin-right: 0;
        margin-bottom: 2rem;
      }
    }
  }
}

.home-advantages {
  width: 100%;
  padding: 10rem 0;

  background-color: #272727;

  @media screen and (max-width: $table-screen) {
    padding: 5rem 0;
  }

  &__inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1.5rem;

    @media screen and (max-width: $mobile-screen) {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__card {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
  }

  &__icon {
    width: 7.5rem;
    height: 7.5rem;

    margin-bottom: 3rem;
  }

  &__title {
    font-size: 2rem;
    font-weight: 600;
    color: white;

    margin: 0;

    text-align: center;
  }
}

.home-what {
  padding: 15rem 0;

  @media screen and (max-width: $table-screen) {
    padding: 10rem 0;
  }

  &__subtitles-row {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3rem;

    @media screen and (max-width: $mobile-screen) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__inner {
    margin-top: 4rem;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3rem;

    @media screen and (max-width: $mobile-screen) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}

.card-text-number {
  width: 100%;

  display: flex;
  align-items: flex-start;
  justify-content: flex-start;

  &__text {
    padding-top: 1rem;
  }

  &__icon {
    width: 6.5rem;
    height: 6.5rem;

    min-width: 6.5rem;
    min-height: 6.5rem;

    background-color: #FC734E;
    border-radius: 50%;

    color: white;
    font-size: 4rem;
    font-weight: 900;

    display: flex;
    align-items: center;
    justify-content: center;

    margin-right: 2rem;
  }
}

.home-why {
  padding: 0;

  &__inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3rem;

    @media screen and (max-width: $mobile-screen) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}

.home-reviews {
  padding: 15rem 0;

  @media screen and (max-width: $mobile-screen) {
    padding: 5rem 0;
  }

  &__inner {
    margin-top: -1.5rem;
    margin-left: -1.5rem;
    margin-right: -1.5rem;
  }

  &__slide {
    padding: 1.5rem;
  }

  &__arrows {
    margin-top: 2.5rem;
  }
}

.home-reviews-card {
  padding: 3rem;
  background: white;

  @media screen and (max-width: $mobile-screen) {
    padding: 2rem;
  }

  &__head {
    display: grid;
    grid-template-columns: 3fr 1fr 4fr;
    grid-row-gap: 1rem;
    align-items: center;
    grid-template-areas:
      "a a a b"
      "c c c c";
  }

  &__name {
    grid-area: a;

    font-size: 2rem;
    font-weight: 900;
    color: black;
  }

  &__date {
    grid-area: b;
    text-align: right;

    font-size: 1.4rem;
    font-weight: 400;
    color: $color-main;
  }

  &__place {
    grid-area: c;
    font-size: 1.4rem;
    font-weight: 400;
    color: #858585;
  }

  &__text {
    margin-top: 2rem;
  }
}

.slider-arrow {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__button {
    display: block;

    width: 10rem;
    height: 0.8rem;

    outline: none;
    border: none;
    background: none;
    transition: all 0.05s linear;
    -webkit-tap-highlight-color: transparent;
    cursor: pointer;

    &:hover {
      opacity: 0.7;
    }
  }
}

.form-section {
  padding: 5rem 0;
  background-color: $color-main;

  &__subtitle {
    font-size: 1.6rem;
    font-weight: 600;
    color: white;

    margin-bottom: 3rem;
  }

  &__inner {
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: 2fr 2fr 1fr;

    max-width: 101rem;
    margin-left: auto;
    margin-right: auto;

    @media screen and (max-width: $mobile-screen) {
      grid-template-columns: 1fr;
      grid-gap: 2rem;
    }
  }
}

.home-port {
  padding: 15rem 0;

  @media screen and (max-width: $mobile-screen) {
    padding: 5rem 0;
  }

  &__arrows {
    width: 100%;
    margin-top: 2rem;
    display: none;

    align-items: center;
    justify-content: space-between;

    @media screen and (max-width: $mobile-screen) {
      display: flex;
    }
  }

  &__inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 3rem;

    @media screen and (max-width: $table-screen) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media screen and (max-width: $mobile-screen) {
      display: block;
    }
  }

  &__img {
    padding-bottom: 100%;
    display: block;

    @media screen and (max-width: $mobile-screen) {
      height: calc(100vw - 4rem);
      padding-bottom: 0;
    }

    &--mock {
      background-color: black;

      &-2 {
        background-color: red;
      }
    }
  }
}

.custom-breadcrumbs {
  margin: 3rem 0;
  color: white;
}

.product-promo {

  &__inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3rem;
    align-items: center;

    @media screen and (max-width: $table-screen) {
      grid-template-columns: 1fr;
    }
  }

  &__img-main {
    display: block;
    width: 100%;
    height: 65rem;

    @media screen and (max-width: $mobile-screen) {
      height: 0;
      padding-bottom: 100%;
    }
  }

  &__title {
    font-size: 3.1rem;
    font-weight: 900;
    color: white;
    margin: 0;
    padding: 0;

    @media screen and (max-width: $mobile-screen) {
      font-size: 2.5rem;
    }
  }

  &__imgs-small {
    width: 49%;
    margin-top: 4rem;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 1rem;

    @media screen and (max-width: $table-screen) {
      width: 100%;
    }

    @media screen and (max-width: $mobile-screen) {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__img-small {
    width: 100%;
    height: 0;
    padding-bottom: 100%;

    display: block;
  }

  &__price-action {
    margin-top: 3.5rem;

    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  &__price {
    font-size: 3.1rem;
    font-weight: 900;
    color: white;

    margin-right: 3rem;
  }

  &__table-heading {
    margin-top: 3.5rem;

    font-size: 2.1rem;
    font-weight: 900;
    color: white;
  }

  &__table-row {
    margin-top: 3.5rem;

    display: flex;
    align-items: flex-end;
    justify-content: space-between;


    &-text {
      font-size: 1.8rem;
      font-weight: 400;
      color: white;
      min-width: max-content;

      &--first {
        margin-right: 1rem;
      }

      &--last {
        margin-left: 1rem;
      }
    }

    &-line {
      height: 0.2rem;
      background-color: white;
      opacity: 0.34;
      width: 100%;
      transform: translateY(-0.5rem);
    }
  }
}

.product-tabs {
  padding: 15rem 0;

  @media screen and (max-width: $mobile-screen) {
    padding: 5rem 0;
  }

  &__elem {
    display: flex;
    align-items: center;
    justify-content: flex-start;

    @media screen and (max-width: $mobile-screen) {
      flex-wrap: wrap;
      margin: -1rem -2rem;
      margin-bottom: 2rem;
    }

    padding: 0;
    margin: 0 0 4rem;
  }

  &__elem-item {
    width: max-content;
    list-style-type: none;
    font-size: 2.1rem;
    font-weight: 400;
    opacity: 0.6;
    color: white;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    outline: none;

    position: relative;

    transition: all 0.05s linear;

    @media screen and (max-width: $mobile-screen) {
      padding: 1rem 2rem;
    }

    &:hover {
      opacity: 0.8;
    }

    &--active {
      opacity: 1;

      &:after {
        content: "";
        display: block;
        width: 100%;
        height: 0.2rem;

        background-color: $color-main;
      }
    }


    &:not(:last-child) {
      margin-right: 2rem;
    }
  }

  &__body {
    width: 100%;
    max-width: 75rem;

    &--max {
      max-width: none;
    }

    &-title {
      margin: 0 0 3rem;
      padding: 0;

      font-size: 2.1rem;
      font-weight: 900;
      color: white;
    }

    &-paragraph {
      font-size: 1.6rem;
      font-weight: 600;
      color: white;

      &:not(:last-child) {
        margin-bottom: 3rem;
      }
    }

    &-grid {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 3rem;

      @media screen and (max-width: $mobile-screen) {
        grid-template-columns: 1fr;
      }
    }
  }
}

.product-port {
  .home-port {
    padding: 0 0 15rem;
  }
}

.catalog-section {
  padding-bottom: 10rem;

  &__inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 3rem;

    @media screen and (max-width: $table-screen) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media screen and (max-width: $mobile-screen) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}

.catalog-card {
  display: block;

  &__img {
    width: 100%;
    padding-bottom: 100%;

    margin-bottom: 1.5rem;
  }

  &__text {
    font-size: 1.6rem;
    font-weight: 600;
    color: white;

    margin-bottom: 1.5rem;

    &--no {
      margin-bottom: 0;
    }
  }
}

.u-mock {
  background-color: black;
}

.blog-board {
  width: 100%;
  padding: 5rem 0 15rem;

  @media screen and (max-width: $mobile-screen) {
    padding-bottom: 10rem;
  }

  &__inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 3rem;

    @media screen and (max-width: $table-screen) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: $mobile-screen) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}

.blog-post-card {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-direction: column;

  padding: 2rem 1.5rem;

  width: 100%;
  height: 100%;

  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  color: white;

  &__promo {
    padding-bottom: 75%;
    margin: -2rem -1.5rem;

    margin-bottom: 2rem;
    background-color: black;
  }

  &__top {
    margin-bottom: 2rem;
  }

  &__bot {

  }

  &__title {
    font-size: 2rem;
    margin-bottom: 2rem;
  }

  &__text {
    font-size: 1.6rem;
  }

  &__action {
    max-width: 17rem;
  }
}

.blog-main {
  padding: 3rem 0 10rem;

  background: url("../images/content/about/bg.jpg") center no-repeat;
  background-size: cover;

  color: white;

  &__subtitle {
    font-size: 3.1rem;
    font-weight: 600;

    max-width: 77rem;
  }

  &__text {
    font-size: 2rem;
  }

  &__row {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1000000;

  width: 100vw;
  height: 100vh;

  background-color: rgba(00, 00, 00, 0.9);

  display: flex;
  align-items: center;
  justify-content: center;

  opacity: 0;
  visibility: hidden;

  transition: all 0.03s linear;

  @media screen and (max-width: $table-screen) {
    padding: 2rem;
  }

  &-active {
    opacity: 1;
    visibility: visible;
  }

  &__form {
    width: 56.3rem;
    height: 59rem;

    background: linear-gradient(180deg, #262626 0%, rgba(38, 38, 38, 0.8) 100%);
  }
}

.modal-form {
  padding: 5rem;
  position: relative;

  &__close {
    width: 2.6rem;
    height: 2.6rem;

    position: absolute;
    top: 2rem;
    left: auto;
    right: 2rem;

    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    outline: none;
    transition: all 0.05s linear;

    &:hover {
      opacity: 0.5;
    }
  }

  @media screen and (max-width: $table-screen) {
    padding: 5rem 3rem;
  }

  &__title {
    font-size: 3.8rem;
    font-weight: 900;
    color: white;
    margin-top: 0;
    text-align: center;
  }

  &__row {

    &:not(:last-child) {
      margin-bottom: 3rem;
    }

    .input-text,
    .input-submit {
      width: 100%;
    }

    .input-submit {
      background-color: #FC734E;

      &:hover {
        box-shadow: 1px 4px 16px #FC734E;
      }
    }

    .input-area {
      height: 9.3rem;
    }
  }
}

.home-calc {

  margin-top: 10rem;
  margin-bottom: 3rem;

  &__form {
    background-color: #FC734E;
  }
}

.home-calc-form {
  max-width: 101rem;
  width: 100%;

  padding: 4rem 1rem 9rem;

  margin-left: auto;
  margin-right: auto;
  color: white;

  @media screen and (max-width: $mobile-screen) {
    padding-bottom: 3rem;
  }

  &__title {
    margin-top: 0;
    font-size: 2rem;
    font-weight: 900;
    margin-bottom: 3rem;

    @media screen and (max-width: $mobile-screen) {
      font-size: 2.2rem;
      margin-bottom: 3rem;
      text-align: center;
    }
  }

  &__subtitle {
    font-size: 1.6rem;
    font-weight: 200;
    line-height: 3rem;
    margin-bottom: 4rem;

    @media screen and (max-width: $mobile-screen) {
      font-size: 1.8rem;
      margin-bottom: 3rem;
      text-align: center;
    }
  }

  &__value {
    font-size: 2rem;
    font-weight: 400;

    @media screen and (max-width: $mobile-screen) {
      font-size: 2.4rem;
      text-align: center;
    }
  }

  &__dec-wrapper {
    margin-bottom: 2.5rem;
  }

  &__dec {
    width: 100%;
    height: 1rem;
    margin-top: 1.5rem;

    @media screen and (max-width: $mobile-screen) {
      height: 1.4rem;
      margin-top: 2.4rem;
    }

    &--mobile {
      display: none;

      @media screen and (max-width: $mobile-screen) {
        display: block;
      }
    }

    &--des {
      display: block;

      @media screen and (max-width: $mobile-screen) {
        display: none;
      }
    }
  }

  &__dec-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;

    font-size: 1.5rem;
    font-weight: 600;

    margin-top: 1rem;
  }

  &__inputs-grid {
    display: grid;
    grid-template-columns: 360 / 1010 * 100% 360 / 1010 * 100% 230 / 1010 * 100%;
    grid-gap: 3rem;
    grid-row-gap: 2rem;
    grid-template-areas:
  'AA AA AA'
  'NN TT SS';

    @media screen and (max-width: $mobile-screen) {
      grid-template-columns: 1fr;
      grid-template-areas: 'AA'
      'NN'
      'TT'
      'SS';

      margin-top: 4rem;
    }

    .input-area {
      width: 100%;
      max-width: 100%;
      min-width: 100%;

      height: 4.8rem;
      min-height: 4.8rem;
      max-height: 4.8rem;
    }

    .input-text {
      width: 100%;
    }

    .input-submit {
      width: 100%;
    }
  }

  &__area {
    grid-area: AA;
  }

  &__name {
    grid-area: NN;
  }

  &__tel {
    grid-area: TT;
  }

  &__submit {
    grid-area: SS;
  }
}

.my-slider {
  height: 0.7rem;
  width: 100%;
  background-color: #fff;
  position: relative;

  margin-top: 2.8rem;
}

.my-slider__handle {
  position: absolute;
  width: 1.6rem;
  height: 1.6rem;

  top: 50% !important;
  transform: translate(0, -50%);

  background: white;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 50%;

  cursor: pointer;
  -webkit-tap-highlight-color: transparent;

  &:after {
    width: 1.6rem;
    height: 1.6rem;
  }
}

.my-slider__fill {
  background-color: white;
  height: 100%;
}

.slick-track {
  display: flex;
  align-items: stretch;
}

.home-reviews-card {
  height: 100%;
}

.slick-slide {
  height: auto;
}

.b-breadcrumbs__link {
  font-size: 1.4rem;
  font-weight: 400;
}

.b-breadcrumbs__text {
  color: #B5B5B5;
  font-size: 1.4rem;
  font-weight: 400;
  opacity: 0.5;
}